.content.qr__history .qr__history__search {
  grid-column: span 4 / span 4;
}

.qr__history table.gridjs-table tbody tr:hover {
  background: #f4f4f4;
  cursor: pointer;
}

.content.qr__history section.qr__history__search {
  grid-column: span 4 / span 4;
}

.content.qr__history section.qr__history__search .search__wrap {
  display: flex;
  justify-content: flex-start;
  align-items: flex-end;
  margin-bottom: 24px;
}

.content.qr__history section.qr__history__search .search__wrap .input__wrap>dl {
  width: 100%;
}

.content.qr__history section.qr__history__search .search__wrap>.input__wrap {
  width: 50%;
}

.content.qr__history section.qr__history__search .search__wrap .input__wrap input {
  width: 100%;
}

.content.qr__history section.qr__history__search .search__wrap dl {
  margin-right: 24px;
}

.content.qr__history section.qr__history__search .search__wrap dl:last-of-type {
  margin-right: 0px;
}

.content.qr__history section.qr__history__search .search__wrap dt {
  margin-bottom: 12px;
}

.content.qr__history section.qr__history__search .search__wrap>div {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-right: 18px;
}

.content.qr__history section.qr__history__search .search__wrap>div.left {
  width: 100%;
  justify-content: flex-start;
  align-items: flex-end;
}

.content.qr__history section.qr__history__search .search__wrap .selector__wrap dd {
  display: flex;
  justify-content: flex-start;
  align-items: center;
}

.content.qr__history section.qr__history__search .selector__wrap .react-datepicker-wrapper:first-of-type {
  margin-right: 16px;
}

.content.qr__history section.qr__history__search .search__wrap>div.left>.selector__wrap {
  display: flex;
  margin-right: 24px;
}

.content.qr__history section.qr__history__search .search__wrap>div.left>.input__wrap {
  width: 40%;
  margin-right: 24px;
}

.content.qr__history table.gridjs-table {
  width: 100%;
  min-width: 2100px;
}

/* grid table min width */

#companies.qr__history table.gridjs-table {
  min-width: 1800px !important;
}

#sysAdmin.qr__history table.gridjs-table {
  min-width: 1800px !important;
}

#project.qr__history table.gridjs-table {
  min-width: 1600px !important;
}

#admin.qr__history table.gridjs-table {
  min-width: 1600px !important;
}

#department.qr__history table.gridjs-table {
  min-width: 1700px !important;
}

#station.qr__history table.gridjs-table {
  min-width: 2400px !important;
}

/* // grid table min width */
@media (max-width: 1200px) {
  .content.qr__history section.qr__history__search .search__wrap .input__wrap input {
    min-width: 180px;
  }
}

@media (max-width: 1040px) {
  .content.qr__history section.qr__history__search .search__wrap>div {
    margin-right: 0px;
  }

  .content.qr__history section.qr__history__search .search__wrap {
    flex-direction: column;
  }

  .content.qr__history section.qr__history__search .search__wrap.groups {
    flex-direction: row;
  }

  .content.qr__history section.qr__history__search .search__wrap .input__wrap {
    width: 100%;
  }

  .content.qr__history section.qr__history__search .search__wrap>div {
    width: 100%;
    margin-bottom: 24px;
  }

  .content.qr__history section.qr__history__search .search__wrap.groups>div {
    margin-bottom: 0;
  }

  .content.qr__history section.qr__history__search .search__wrap .input__wrap dl {
    width: 49%;
  }

  .content.qr__history section.qr__history__search .search__wrap.groups .input__wrap dl {
    width: 100%;
  }

  .content.qr__history section.qr__history__search .search__wrap>button {
    width: 100%;
  }

  .content.qr__history section.qr__history__search .search__wrap>.add__btn {
    width: auto;
  }

  .content.qr__history section.qr__history__search .search__wrap .selector__wrap {
    justify-content: flex-start;
  }
}

@media (max-width: 800px) {
  .content.qr__history section.qr__history__search .search__wrap .selector__wrap dd {
    flex-direction: column;
  }

  .content.qr__history section.qr__history__search .search__wrap .selector__wrap {
    flex-direction: column;
  }

  .content.qr__history section.qr__history__search .search__wrap .selector__wrap dl {
    margin-right: 0px;
    margin-bottom: 12px;
    width: 100%;
  }

  .content.qr__history section.qr__history__search .search__wrap.groups {
    flex-direction: column;
  }

  .content.qr__history section.qr__history__search .search__wrap>div.left {
    flex-direction: column;
    margin-bottom: 24px;
  }

  .content.qr__history section.qr__history__search .search__wrap>div.left>.selector__wrap {
    margin-right: 0px;
    width: 100%;
    margin-bottom: 12px;
  }

  .content.qr__history section.qr__history__search .search__wrap>div.left>.input__wrap {
    width: 100%;
    margin-right: 0;
  }

  .content.qr__history section.qr__history__search .search__wrap>div.left>button {
    width: 100%;
  }

  .content.qr__history section.qr__history__search .search__wrap.groups>button,
  .content.qr__history section.qr__history__search .search__wrap.groups>a {
    width: 100%;
  }

  .content.qr__history section.qr__history__search .selector__wrap .react-datepicker-wrapper:first-of-type {
    margin-right: 0px;
    margin-bottom: 12px;
  }

  .react-datepicker__input-container>input {
    max-width: none;
    width: 100%;
  }

  .content.qr__history section.qr__history__search .selector__wrap .react-datepicker-wrapper {
    width: 100%;
  }

  .content.qr__history section.qr__history__search .search__wrap .selector__wrap dl dd {
    width: 100%;
  }

  .content.qr__history section.qr__history__search .selector__wrap .custom__select {
    width: 100%;
  }

  .content.qr__history section.qr__history__search .search__wrap>.input__wrap {
    flex-direction: column;
  }

  .content.qr__history section.qr__history__search .search__wrap .input__wrap dl {
    margin-right: 0px;
    width: 100%;
  }

  .content.qr__history section.qr__history__search .search__wrap .input__wrap dl:first-of-type {
    margin-bottom: 12px;
  }

  .content.qr__history section.qr__history__search .search__wrap>div {
    margin-bottom: 12px;
  }
}